body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  background-color: #2f3136;
  color: #fff;
  padding-bottom: 250px;
}
a{
  color: #fff;
  text-decoration: none;
}
a:active{
  color: #fff;
}

header{
  background-color: cornflowerblue;
  position: fixed;
  width: 100%;
  top:0%;
  height: 80px;
  z-index: 3;
}
.headerContent{
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
}
.headerContent div {
  text-align: center;
  margin: 2.5em;
  padding: 0.5em;
  border-radius: 8px;
  transition: all 0.5s;
  cursor: pointer;
  position: relative;
}
.headerContent > div:hover{
  background-color: #5287eb;
  border:1px solid#fff;
}
.headerContent > div:after{
  content: 'Go';
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
  left: 100%;
}
.headerContent > div:hover:after{
  opacity: 1;
  left: 108%;
}

.Content{
  margin-top: 80px;
}
nav{
  position: absolute;
  top: 100%;
  display: flex;
  justify-content: space-around;
  width: 100%;
  font-size: 18px;
  font-weight: bold;

}
nav a {
  width: 25%;
  text-align: center;
  padding: 0.5em;
  background-color: rgb(113, 202, 224);
  border-radius: 0px 0px 15px 15px;
  transition: all 1s;
  cursor: pointer;
  letter-spacing: 5px;
}
nav > a:hover{
  background-color: aquamarine;
}
nav > a:active{
  transition: all 0.1s;
  background-color: chartreuse;
}